import { FC } from 'react'
import * as React from 'react'
import {
  ImagePlaceholder,
  URLLinkCardLoadingState,
  URLLinkCardHeight,
} from './Base'
import { AbstractCardLayout } from '@ali/we-toolbar'

export const URLLinkCardLoading: FC<URLLinkCardLoadingState> = ({
  isSelected,
  cardStyle,
}) => {
  // 如果是 indoc，因为有 border，所以需要减去 2px
  const height =
    cardStyle === 'popup' ? URLLinkCardHeight : URLLinkCardHeight - 2

  return (
    <AbstractCardLayout
      isSelected={isSelected}
      height={height}
      type="loading"
      line="double"
      style={
        cardStyle === 'popup'
          ? {
              boxShadow:
                '0px 8px 24px 0px rgba(0, 0, 0, 0.16),0px 0px 1px 0px rgba(0, 0, 0, 0.24)',
              border: 'none',
            }
          : {}
      }
      icon={<ImagePlaceholder />}
    />
  )
}
