import React, { useCallback, useState } from 'react'

export type CardDisplayStyle = 'popup' | 'indoc'

export type URLLinkCardBaseState = {
  readonly: boolean // 只读模式
  isMobile: boolean // 是否是移动端
  url: string // 应用层会保证 url 非空
  isSelected?: boolean

  // 卡片展示风格，popup 表示是 hover 以后弹出的卡片
  // indoc 表示是文档中的卡片
  cardStyle: CardDisplayStyle
  locale: {
    copyTooltip?: string
    unlinkTooltip?: string
    editTooltip?: string
    errorDesc?: string
    error?: string
  }
  onSwitchStyle?: (style: CardDisplayStyle, viewType: 'text' | 'abstract' | 'preview') => void // 切换内容展示风格
  onOpenURL?: (url: string) => void // 打开 URL
  editBtnTestID?: string
  copyBtnTestID?: string
  cutBtnTestID?: string
  unlinkBtnTestID?: string
  deleteBtnTestID?: string
  switchStyleBtnTestID?: string
  linkStyleBtnTestID?: string
  cardStyleBtnTestID?: string
  cardTestID?: string
  enablePreview?: boolean
}

export interface URLInfo {
  title: string // 可以为空字符串
  desc: string //
  imgURL: string
}

export type URLLinkCardLoadingState = {
  state: 'loading'
} & URLLinkCardBaseState

export type URLLinkCardErrorState = {
  state: 'error'
  urlInfo: URLInfo
  onRefresh: () => void // 刷新
  onEdit?: () => void // 编辑
  onCopy?: () => void // 复制
  onCut?: () => void
  onDelete?: () => void
  onUnlink?: () => void // 取消链接
    /**
   * 自定义滚动容器
   */
  getContainer?: () => HTMLElement
} & URLLinkCardBaseState

export type LinkType = 'gist' | 'framer' | 'figma' | 'yuque'

export type URLLinkCardNormalState = {
  // 如果传入了链接类型，那么图片将根据链接类型来选择
  // 忽略传入的 urlInfo.imgURL
  linkType?: LinkType

  state: 'normal'
  urlInfo: URLInfo
  onEdit?: () => void // 编辑
  onCopy?: () => void // 复制
  onCut?: () => void
  onDelete?: () => void
  onUnlink?: () => void // 取消链接
  /**
   * 自定义滚动容器
   */
  getContainer?: () => HTMLElement
} & URLLinkCardBaseState

export const URLLinkCardHeight = 80



export const ImagePlaceholder = () => {
  const [isError, setError] = useState(false);
  const onImageError = useCallback(() => {
    setError(true);
  }, []);

  if (isError) {
    return (
      <svg width="40" height="40" viewBox="0 0 40 40" style={{ width: 64, height: 64 }} fill="none" xmlns="http://www.w3.org/2000/svg">
        <rect width="40" height="40" rx="6" fill="#7E868E" fill-opacity="0.12" />
        <path d="M20.7071 23.5355L18.5858 25.6568C17.4142 26.8284 15.5147 26.8284 14.3432 25.6568C13.1716 24.4853 13.1716 22.5858 14.3432 21.4142L16.4645 19.2929C17.636 18.1213 19.5355 18.1213 20.7071 19.2929L21.4142 20L22.121 19.2925L21.4142 18.5858C19.8521 17.0237 17.3195 17.0237 15.7574 18.5858L13.636 20.7071C12.0739 22.2692 12.0739 24.8019 13.636 26.3639C15.1981 27.926 17.7308 27.926 19.2929 26.3639L21.4142 24.2426L20.7071 23.5355Z" fill="#171A1D" fill-opacity="0.4" />
        <path d="M19.2929 16.4645L21.4142 14.3431C22.5782 13.1791 24.5602 13.2465 25.6569 14.3431C26.7535 15.4398 26.8209 17.4218 25.6569 18.5858L23.5355 20.7071C22.364 21.8787 20.4645 21.8787 19.2929 20.7071L18.5858 20L17.8789 20.7074L18.5858 21.4142C20.1479 22.9763 22.6805 22.9763 24.2426 21.4142L26.364 19.2929C27.9336 17.7232 27.8358 15.1079 26.364 13.636C24.8921 12.1642 22.2768 12.0664 20.7071 13.636L18.5858 15.7573L19.2929 16.4645Z" fill="#171A1D" fill-opacity="0.4" />
      </svg>
    )
  }

  return (
    <img
      width={'64px'}
      height={'64px'}
      src="https://img.alicdn.com/tfs/TB1vTtAoxvbeK8jSZPfXXariXXa-192-192.png"
      onError={onImageError}
    />
  )

};


export class UnsupportedValueError extends Error {
  constructor(value: never) {
    super('Unsupported value: ' + value)
  }
}
