import React, { FC } from 'react'
import {
  URLLinkCardErrorState,
  ImagePlaceholder,
  URLLinkCardHeight,
} from './Base'
import { AbstractCardLayout } from '@ali/we-toolbar'
import { CopyIcon, EditIcon, UnlinkIcon } from '~/icon'

export const URLLinkCardError: FC<URLLinkCardErrorState> = (props) => {
  const {
    isSelected,
    cardStyle,
    locale,
    onRefresh,
    urlInfo,
    url,
    readonly,
    onOpenURL,
    onSwitchStyle,
    editBtnTestID,
    unlinkBtnTestID,
    copyBtnTestID,
    cutBtnTestID,
    deleteBtnTestID,
    switchStyleBtnTestID,
    linkStyleBtnTestID,
    cardStyleBtnTestID,
    enablePreview,
    getContainer,
  } = props

  const handleRefresh = (evt: React.MouseEvent) => {
    evt.stopPropagation()
    onRefresh()
  }

  const onStyleChange = (num: number) => {
    switch (num) {
      case 0:
        onSwitchStyle?.('popup', 'text')
        break;

      case 1:
        onSwitchStyle?.('indoc', 'abstract');
        break;

      case 2:
        onSwitchStyle?.('indoc', 'preview');
        break;
    }
  }

  // 如果是 indoc，因为有 border，所以需要减去 2px
  const height =
    cardStyle === 'popup' ? URLLinkCardHeight : URLLinkCardHeight - 2

  return (
    <AbstractCardLayout
      isSelected={isSelected}
      height={height}
      type="failure"
      title={urlInfo?.title || locale.error}
      icon={<ImagePlaceholder />}
      toolbarVisible={cardStyle === 'popup' ? true : undefined}
      titleClick={() => onOpenURL?.(url)}
      failureText={locale.errorDesc}
      getContainer={getContainer}
      refresh
      style={
        cardStyle === 'popup'
          ? {
            boxShadow:
              '0px 8px 24px 0px rgba(0, 0, 0, 0.16),0px 0px 1px 0px rgba(0, 0, 0, 0.24)',
            border: 'none',
          }
          : {}
      }
      refreshClick={handleRefresh}
      toolbarConfigs={
        readonly
          ? [
            {
              type: 'button',
              title: locale.copyTooltip,
              icon: <CopyIcon />,
              onClick: props.onCopy,
              testId: copyBtnTestID,
            },
          ]
          : [
            {
              type: 'button',
              title: locale.editTooltip,
              icon: <EditIcon />,
              onClick: props.onEdit,
              testId: editBtnTestID,
            },
            {
              type: 'button',
              title: locale.unlinkTooltip,
              icon: <UnlinkIcon />,
              onClick: props.onUnlink,
              testId: unlinkBtnTestID,
            },
            {
              type: 'style',
              options: enablePreview ? ['text', 'abstract', 'preview'] : ['text', 'abstract'],
              optionsTestId: {
                text: linkStyleBtnTestID || '',
                abstract: cardStyleBtnTestID || '',
                preview: '',
              },
              onChange: onStyleChange,
              defaultValue: cardStyle === 'popup' ? 0 : 1,
              testId: switchStyleBtnTestID,
            },
            {
              type: 'more',
              menu: [
                [
                  {
                    name: 'delete',
                    onClick: props.onDelete,
                    testId: deleteBtnTestID,
                  },
                  {
                    name: 'copy',
                    onClick: props.onCopy,
                    testId: copyBtnTestID,
                  },
                  {
                    name: 'cut',
                    onClick: props.onCut,
                    testId: cutBtnTestID,
                  },
                ],
              ],
            },
          ]
      }
    />
  )
}
