import { LinkType, UnsupportedValueError, URLInfo } from './components/Base'

interface URLPatternBase {
  // NOTE(cj): 这个字段目前没有实际用处，当做备注用
  name: string
  imgURL: string
}

interface URLDomainPattern extends URLPatternBase {
  domains: string[]
  regexp: RegExp
}

interface URLCustomPattern extends URLPatternBase {
  test: (url: string) => boolean
}

type URLPattern = URLCustomPattern | URLDomainPattern

